import React from 'react'
import { Link } from 'gatsby'

const PostSnippet = ({ post }) => (
  <div>
    <h1>
      <Link to={post.frontmatter.path}>{post.frontmatter.title}</Link>
    </h1>
    <p>
      <strong>{post.frontmatter.date}</strong>
    </p>
    <p>{post.excerpt}</p>
  </div>
)

export default PostSnippet
